import { Col, Row, Table, Pagination } from "antd";
import React from "react";

const CTableReport = ({
  columns,
  data = [],
  loading,
  scroll,
  onShowSizeChange,
  current,
  total = 0,
  ...rest
}) => {
  return (
    <Row gutter={4} style={{ flex: 1 }}>
      <Col span={24} style={{ paddingTop: "20px" }}>
        <Table
          columns={columns}
          dataSource={data}
          size="small"
          rowKey="id"
          bordered
          pagination={false}
          loading={loading}
          scroll={scroll}
          {...rest}
        />
      </Col>
      <Col span={24} style={{ paddingTop: "10px" }}>
        <Pagination
          style={{ float: "right" }}
          showSizeChanger
          onShowSizeChange={onShowSizeChange}
          defaultCurrent={1}
          total={total}
          current={current}
          defaultPageSize={15}
          pageSizeOptions={["15", "30", "50", "100", "200"]}
          onChange={onShowSizeChange}
          size="small"
        />
      </Col>
    </Row>
  );
};

export default CTableReport;
